import React from 'react';

import { Container, Grid } from '@mui/material';

import CSS from '../../assets/css modules/weblog/Categorys.module.css';

import OfferBox from './../common/OfferBox';
import CourseInfo from './CourseInfo';
import CourseDetails from './CourseDetails';
import SocialBox from './ShareBox';
import Comment from '../common/Commenting/Comment';
import RelatedCourse from '../common/RelatedContentBox';
import BlogImage from '../../assets/image/BlogImage.jpg';
import Comments from '../common/Commenting/Comments';
class CourseContainer extends React.Component {
  render() {
    return (
      <Container
        maxWidth="lg"
        className={CSS.container}
        style={{ padding: '0px' }}
      >
        <Grid
          container
          maxWidth="lg"
          columns={{ xs: 12, sm: 12, md: 12 }}
          direction="row-reverse"
          justifyContent="center"
          justifySelf="center"
          alignItems="flex-start"
          pb={2}
        >
          <Grid item xs={12} sm={12} md={4} lg={4}>
            <Grid container>
              <Grid
                item
                xs={12}
                sm={12}
                md={12}
                lg={12}
                px={1}
                style={{
                  position: 'sticky !important',
                  top: '101px !important',
                }}
              >
                <CourseDetails />
              </Grid>
            </Grid>
            <Grid item xs={12} sm={12} md={12} lg={12} py={2} px={1}>
              <SocialBox />
            </Grid>
          </Grid>
          <Grid
            item
            xs={12}
            sm={12}
            md={8}
            lg={8}
            //   px={1}
            //   style={{ backgroundColor: '#4895EF' }}
          >
            <OfferBox
              name={'  '}
              backgrColor={'#fff'}
              textColor={'#fff'}
            />
            <CourseInfo
              name={'     '}
              text={
                '                                                                                                                                     .'
              }
              backgrColor={'#FFFFFF'}
              textColor={'#212529'}
            />
            <Comment
              name={'  '}
              backgrColor={'#D1D1D1'}
              textColor={'#212529'}
            />
            <Comments
              name={'  '}
              backgrColor={'#FFFFFF'}
              textColor={'#212529'}
            />
          </Grid>
        </Grid>
        <h4 style={{ color: '#000', textAlign: 'center' }}>  </h4>
        <Grid
          container
          maxWidth="lg"
          direction="row"
          justifyContent="center"
          justifySelf="center"
          alignItems="center"
          spacing={{ xs: 3, md: 4 }}
          pt={3}
          pb={10}
        >
          <Grid item xs={12} sm={6} md={3} lg={3}>
            <RelatedCourse
              src={BlogImage}
              name={'   '}
              masterName={' '}
              readTime={' '}
              date={'  '}
              tag={' '}
              TagColor={'#4895EF'}
            />
          </Grid>
          <Grid item xs={12} sm={6} md={3} lg={3}>
            <RelatedCourse
              src={BlogImage}
              name={'   '}
              masterName={' '}
              readTime={' '}
              date={'  '}
              tag={' '}
              TagColor={'#4895EF'}
            />
          </Grid>
          <Grid item xs={12} sm={6} md={3} lg={3}>
            <RelatedCourse
              src={BlogImage}
              name={'   '}
              masterName={' '}
              readTime={' '}
              date={'  '}
              tag={' '}
              TagColor={'#4895EF'}
            />
          </Grid>
          <Grid item xs={12} sm={6} md={3} lg={3}>
            <RelatedCourse
              src={BlogImage}
              name={'   '}
              masterName={' '}
              readTime={' '}
              date={'  '}
              tag={' '}
              TagColor={'#4895EF'}
            />
          </Grid>
        </Grid>
      </Container>
    );
  }
}

export default CourseContainer;
